import React, { Component } from 'react';
import './App.css';
import './css/main.css';
import FacebookLogin
  from 'react-facebook-login/dist/facebook-login-render-props';
import Table from './Table';
import Form from './Form';
import sharepost from './sharepost.png';

class App extends Component {
  state = {
    characters: []
  };

  removeCharacter = (index) => {
    const {characters} = this.state;

    this.setState({
      characters: characters.filter((character, i) => {
        return i !== index
      })
    })
  }

  handleSubmit = (character) => {
    this.setState({characters: [...this.state.characters, character]});
  };

  initFB = ({ appId, version }) => {
    window.FB.init({
      appId      : appId,
      cookie     : true,
      xfbml      : true,
      version    : version,
    });
  };

  shareGamePost = async ({ accessToken }) => {
    const href = `${window.location.origin}/lucky_draw_game_poster.html`;
    window.FB.ui(
      {
        display: 'popup',
        method: 'share',
        href,
      },
      res => {
        if (!res || (res && res.error_message)) {
          alert('something went wrong');
        } else if (res && accessToken) {
          console.log('accessToken', accessToken);
        }
      },
    );
  };

  loginAndShare = () => {
    window.FB.login(response => {
      if (response.status === 'connected') {
        this.shareGamePost({ ...response.authResponse });
      }
    }, {
      scope: 'public_profile,user_posts',
      auth_type: 'rerequest',
    });
  };

  actionByLoginStatus = () => {
    window.FB.getLoginStatus(response => {
      if (response.status === 'connected') {
        this.shareGamePost({ ...response.authResponse });
      } else {
        this.loginAndShare();
      }
    });
  };

  handleClick = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    await this.initFB(
      { appId: '942643939925036', version: 'v11.0' },
    );
    this.actionByLoginStatus();
  };

  render(){
    const { characters } = this.state;
    
    return (
      <div className="App">
        <h1 className="site-heading">Hello, React!</h1>
        <div className="container">
          <Table 
            characterData={characters}
            removeCharacter={this.removeCharacter} />
          <Form handleSubmit={this.handleSubmit}/>
          <button id="btnCopy">Copy Image Hidden</button>
          <img id="copyImage2" src={sharepost} hidden/>

          <FacebookLogin
            appId="942643939925036"
            autoLoad={false}
            version="11.0"
            fields="name,id"
            scope="public_profile,user_posts"
            returnScopes
            render={() => (
              <button
                type="button"
                onClick={(e) => this.handleClick(e)}
              >
                Share With Facebook
              </button>
            )}
          />
        </div>
      </div>
    );
  }
}

export default App;
